<template>
  <v-container>
    <v-row class="text-center">
      <v-col cols="12">
        <v-img
        :src="'https://www.freepnglogos.com/uploads/trophy-png/trophy-award-winner-png-33.png'"
          class="my-3"
          contain
          height="200"
        />
      </v-col>
    </v-row>
    <v-row>
      <v-col md="12" xs="12" sm="12">
        <v-select
          v-model="selectFilter"
          @change="filtered()"
          :items="games"
          label="Jenis Game"
          style="background-color:white;"

          outlined
        ></v-select>
      </v-col>
    </v-row>
    <v-row>
      <v-col md="12" xs="12" sm="12" v-for="(game, index) in datalist" :key="index">
        <v-card
          elevation="5"
          style="border-radius:20px; border: 2px dashed #ae88ac;"
        >
        <v-card-title style="padding-bottom:0px; display:flex; justify-content:center;">
          <span style="color:#ae88ac;font-weight:bold; font-size:34px;">{{game.Game}}</span>
          <!-- {{game}} -->
        </v-card-title>
          <v-list rounded>
            <v-list-item-group
              v-model="selectedItem"
              color="primary"
            >
              <v-list-item
                v-for="(item, i) in game.Scoring"
                :key="i"
                v-show="item[0] !== 'Game'"
              >
                <v-list-item-icon v-if="i > 1 || item[1] == 0">
                  <!-- <v-icon v-text="'mdi-trophy-award'" v-if="i === 1"></v-icon> -->
                  
                  <!-- <v-icon v-text="'mdi-trophy-outline'" v-if="i === 2"></v-icon> -->
                  <v-icon v-text="'mdi-snail'" ></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title :style="i == 1 && item[1] > 0 ? {textAlign:'center'} : ''">
                    <v-img
                  v-if="i === 1 && item[1] > 0"
                  :src="'https://cliply.co/wp-content/uploads/2021/03/392103930_CROWN_EMOJI_400px.gif'"
                    contain
                    height="100"
                  />
                    <h2 style="color:#f6d448;" v-if="i === 1 && item[1] > 0">{{ item[0] }}</h2>
                    <h3 v-else>{{ item[0] }}</h3>
                  </v-list-item-title>
                  <v-list-item-subtitle :style="i == 1 && item[1] > 0 ? {textAlign:'center'} : ''">
                  <span style="margin-right:10px; font-size:20px; font-weight:bold; color:#f6d448;" v-if="i === 1 && item[1] > 0">Score : {{item[1]}}</span>
                  <span style="margin-right:10px;" v-else>Score : {{item[1]}}</span>
                  </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-col>
    </v-row>
    <v-dialog
      v-model="dialog"
      persistent
      max-width="290"
    >
      <v-card>
        <v-card-title class="text-h5">
          Update Score {{selectedGames.name}} for {{selectedGames.player}}
        </v-card-title>
        <v-card-actions>
          <v-text-field
            v-model="inputtedScore"
            label="New Score"
            outlined
          ></v-text-field>
          <v-spacer></v-spacer>
          <v-btn
            color="green darken-1"
            text
            @click="dialog = false"
          >
            Cancel
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="updateData()"
          >
            Save
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>
<style>
.v-text-field.v-text-field--enclosed:not(.v-text-field--rounded) > .v-input__control > .v-input__slot, .v-text-field.v-text-field--enclosed .v-text-field__details {
 background:white;
 margin-bottom:0px;
}
</style>
<script>
import axios from 'axios'
  export default {
    name: 'HelloWorld',

    data: () => ({
      selectedItem: 0,
      dialog: false,
      selectedGames: [],
      indexQuery: [],
      datalist: [],
      sourceData: [],
      inputtedScore: 0,
      games: [],
      player: [],
      selectFilter: '',
      items: [
        { text: 'Sasa', icon: 'mdi-trophy-award' },
        { text: 'Audience', icon: 'mdi-trophy-outline' },
        { text: 'Conversions', icon: 'mdi-snail' }
      ],
    }),
    methods: {
      filtered(){
        let arr = this.sourceData
        this.datalist = arr.filter(x => x.Game === this.selectFilter);
      },
      fetch(){
        axios.get('https://script.google.com/macros/s/AKfycbxcysBZIIyw8QPz4XoP9DSUBAHIbjYVRke_NtXYXog-wcWJEbZULcKXoHrCg9JOIq-X/exec').then((response) => {
          let data = response.data
          let gameArr = []
          for(var i = 0; i < data.length; i++) {
            let obj = data[i]
            data[i].Scoring = Object.keys(obj).map((key) => [key, obj[key]]);
            gameArr.push(data[i].Game)
            data[i].Scoring.sort( function( a , b){
                if(a[1] > b[1]) return -1;
                if(a[1] < b[1]) return 1;
                return 0;
            });
          }
          this.sourceData = data
          this.datalist = this.sourceData
          this.games = gameArr.filter(function(item, pos) {
              return gameArr.indexOf(item) == pos
          })
        })
      }
    },
    mounted() {
      this.fetch()
    },
  }
</script>
